<template>
	<div>
		<ContentHeader title="Perawatan" />
		 <div class="content-header">
			<div class="container-fluid">
	            <div class="card">
	              <div class="card-header">
	                <h3 class="card-title"><div>Kunjungan / Antrian Pasien</div></h3>

	                <div class="card-tools">
	                  <button type="button" class="btn btn-tool" data-card-widget="collapse" title="Collapse">
	                    <i class="fas fa-minus"></i>
	                  </button>
	                </div>
	              </div>
	              <div class="card-body" style="display: block;">
	              	<table class="table table-bordered table-hover">
	              		<thead>
	        				    <tr>
	        				      <th scope="col">No</th>
	        				      <th scope="col">Nomor Antrian</th>
	        				      <th scope="col">NAMA</th>
	        				      <th scope="col">NOMOR TELEPON</th>
	        				      <th scope="col">STATUS</th>
	        				      <th scope="col">WAKTU KUNJUNGAN</th>
	        				      <th scope="col">ACTION</th>
	        				    </tr>
	        				  </thead>
	        				  <tbody>
	        				  	<tr v-for="(row, index) in kunjungan" :key="row.id">
	        				  		<td scope="row">{{ increment(index) }}</td>
	        				  		<td>{{ row.nomor }}</td>
	        				  		<td>{{ row.nama_pasien }}</td>
	        				  		<td>{{ row.nomor_telepon }}</td>
	        				  		<td>{{ row.status }}</td>
	        				  		<td>{{ row.tanggal.slice(11, 16) }}</td>
	        				  		<td>
							      	<div class="btn-group">
							      		<router-link :to="`/tambah-data-perawatan/${row.id}`" class="btn btn-sm btn-info text-white">Proses</router-link>
					                </div>
							      </td>
	        				  	</tr>
	        				  </tbody>
	              	</table>
	              	<p class="text-center mt-2">{{cekDataKunjungan}}</p>
	              </div>
	              <!-- /.card-body -->
	              <div class="card-footer" style="display: block;">
	                
	              </div>
	              <!-- /.card-footer-->
	            </div>


	            <div class="card">
	              <div class="card-header">
	                <h3 class="card-title"><div>Pemeriksaan</div></h3>

	                <div class="card-tools">
	                  <button type="button" class="btn btn-tool" data-card-widget="collapse" title="Collapse">
	                    <i class="fas fa-minus"></i>
	                  </button>
	                </div>
	              </div>
	              <div class="card-body" style="display: block;">
	              	<table class="table table-bordered table-hover">
	              		<thead>
	        				    <tr>
	        				      <th scope="col">No</th>
	        				      <th scope="col">Nomor Antrian</th>
	        				      <th scope="col">NAMA</th>
	        				      <th scope="col">NOMOR TELEPON</th>
	        				      <th scope="col">STATUS</th>
	        				      <th scope="col">WAKTU KUNJUNGAN</th>
	        				      <th scope="col">ACTION</th>
	        				    </tr>
	        				  </thead>
	        				  <tbody>
	        				  	<tr v-for="(row, index) in pasienTelahDiperiksa" :key="row.id">
	        				  		<td scope="row">{{ increment(index) }}</td>
	        				  		<td>{{ row.nomor }}</td>
	        				  		<td>{{ row.nama_pasien }}</td>
	        				  		<td>{{ row.nomor_telepon }}</td>
	        				  		<td>{{ row.status }}</td>
	        				  		<td>{{ row.tanggal.slice(11, 16) }}</td>
	        				  		<td>
							      	<div class="btn-group">
					                    <button type="button" class="btn btn-sm btn-info">Proses</button>
					                    <button type="button" class="btn btn-sm btn-info dropdown-toggle dropdown-icon" data-toggle="dropdown" aria-expanded="false">
					                      <span class="sr-only"></span>
					                    </button>
					                    <div class="dropdown-menu" role="menu" style="">
					                      <router-link :to="`/edit-data-perawatan/${row.id}`" class="dropdown-item">Edit</router-link>
					                    </div>
					                  </div>
							      </td>
	        				  	</tr>
	        				  </tbody>
	              	</table>
	              	<p class="text-center mt-2">{{cekDataPasienTelahDiperiksa}}</p>
	              </div>
	              <!-- /.card-body -->
	              <div class="card-footer" style="display: block;">
	                
	              </div>
	              <!-- /.card-footer-->
	            </div>

			</div>
		</div>
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader'

	import { ref, computed, onMounted } from 'vue'
	import axios from 'axios'
	import store from '@/store'

	export default{
		components: {
			ContentHeader: ContentHeader

		},
		setup(){
			const increment = (index) => {
				return index+1
			}

			const format_member = (status) => {
				if (status == 1) {
					return 'Member'
				} else {
					return 'Belum Member'
				}
			}
			const kunjungan = ref()
			const pasienTelahDiperiksa = ref()
			const cekDataKunjungan = ref()
			const cekDataPasienTelahDiperiksa = ref()
			const user = computed(() => store.getters['auth/user'])

	    	const getKunjungan = async () => {
	    		let response = await axios.get('api/kunjungan/getWhereDalamAntrian/' + user.value.cabang_id)
	    		
		        if (response.data == 'kosong') {
		          cekDataKunjungan.value = 'Data Masih Kosong'
		          kunjungan.value = null
		        } else {
		          cekDataKunjungan.value = ''
		          kunjungan.value = response.data
		        }	
	    	}

	    	const getPasienTelahDiperiksa = async () => {
	    		let response = await axios.get('api/kunjungan/getWhereTelahDiperiksa/' + user.value.cabang_id)
	    		
		        if (response.data == 'kosong') {
		          cekDataPasienTelahDiperiksa.value = 'Data Masih Kosong'
		          pasienTelahDiperiksa.value = null
		        } else {
		          cekDataPasienTelahDiperiksa.value = ''
		          pasienTelahDiperiksa.value = response.data
		        }	
	    	}

	    	setInterval(() => {
		    		getKunjungan()
		    		getPasienTelahDiperiksa()
	    		}, 240000)

	    	onMounted(() => {
	    		getKunjungan()
	    		getPasienTelahDiperiksa()
	    	})

	    	return { format_member, getKunjungan, kunjungan, cekDataKunjungan, pasienTelahDiperiksa, getPasienTelahDiperiksa, cekDataPasienTelahDiperiksa, increment }
		}
	}
</script>